//immport lib
import {Map, InfoWindow, Marker, GoogleApiWrapper} from 'google-maps-react';
import { Div, Button, FixedLayout } from '@vkontakte/vkui';
import React from 'react';
import InfoWindowEx from './InfoWindowEx'
import connect from '@vkontakte/vkui-connect';

//import icon and image
import forest from '../img/forest.png';
import one_1 from "../img/one_1.jpg";
import Icon24Replay from '@vkontakte/icons/dist/36/replay';
import one_colum_0 from "../img/one_colum.jpg";
import one_3 from "../img/man.jpg";
import one_3_2 from "../img/man.jpg";
import MyIcon from '../img/map.png'

    const style = {
      width: '100%',
      height: '100%',
      'marginLeft': 'auto',
      'marginRight': 'auto'
    }


export class MapContainer extends React.Component {


  constructor(props) {
    super(props);
  const errorGeo = this.props.geoData;
   this.state = {
    nameButton: "Я на месте",
//    lat:this.props.geoData.lat,
  //  lon:this.props.geoData.lon,
    showingInfoWindow: false,
    activeMarker: {},
    selectedPlace: {},

  };
 }


  onMarkerClick = (props, marker, e) =>{
  switch(marker.id_marker){
     
    case 'one_2':
   
 this.setState({
      selectedPlace: props,
      activeMarker: marker,
      showingInfoWindow: true
    });

    break;

    case 'one_3':
       this.setState({
      selectedPlace: props,
      activeMarker: marker,
      showingInfoWindow: true
    });
    break;
    case 'I':
this.setState({
      selectedPlace: props,
      activeMarker: marker,
      showingInfoWindow: true
    });

    break;
    case 'one_1':
    this.setState({
      selectedPlace: props,
      activeMarker: marker,
      showingInfoWindow: true
    });
    break;
    default:
    console.log("ошибочка");
    break;

  }}
   submit = (e) => {
    console.log(this.state.selectedPlace);
  this.props.checkGeo(this.state.selectedPlace);


   }

   
    
  onMapClicked = (props) => {
    if (this.state.showingInfoWindow) {
      this.setState({
        showingInfoWindow: false,
        activeMarker: null
      })
    }
  };
 

  render=(e)=> {
  
    return (
      <Map google={this.props.google}
      style={style}
      zoom={16}
       initialCenter={{
            lat: 44,
            lng: 39
        }}
		gestureHandling={'greedy'}
    streetViewControl={false}
    mapTypeControl={false}
    zoomControl={false}
          onClick={this.onMapClicked}>

<div>
    <Button size="xl" onClick={this.props.updateGeo} level="commerce">
      <p align="center">Обновить</p>

    </Button>
 </div>     

         <Marker onClick={this.onMarkerClick}
              position={{ lat: 59.934777, lng: 30.302896 }}
              name={"Колонны славы"}
              img={one_1}
              img_1={one_colum_0}
            
              pretitle={"Колонны со статуями Славы в Санкт-Петербурге. Памятник в честь побед Лейб-гвардии Конного полка в Отечественной войне 1812 года был установлен на Конногвардейском бульваре в 1845-1846 годах. Автор статуй — Х. Д. Раух, автор колонн — К. И. Росси."}
              id_marker={"one_1"}
              text={<div style={{"fontWeight":"bold","fontSize":"120%"}}><p align="center">Приготовьтесь, сейчас вы узнате 3 самых важных и интересных фактов об этом месте. И мы начинаем!:))</p><p>Колонны Славы выполнены в ионическом ордере из серого сердобольского гранита.<br/>Летящие статуи богини Ники с лавровыми венками в руках,наверху, были изготовлены в Берлине по проекту скульптора Х. Д. Рауха и подарены императору Николаю I королем Фридрихом Вильгельмом IV в 1845 году в знак ответной благодарностью за копии скульптурных групп «Укрощение коня» П. К. Клодта (оригиналы которых установлены на Аничковом мосту).</p> </div> }
              end={<div style={{"fontWeight":"bold","fontSize":"120%"}}><p>Третий факт: Высота каждой колонны — 12 метров, высота статуи — 2,5 метра.</p></div>}
              pollQuestions={"Как вы думаете какой высоты эти колонны?"}
              answer_1={"12 метров"}
              answer_2={"13 метров"}
              answer_3={"15 метров"}
              but={<Button level={"commerce"} onClick={this.submit}>{this.state.nameButton}</Button>}
                 />
              


         <Marker onClick={this.onMarkerClick}
                pretitle={'Здание Конногвардейского манежа в Санкт-Петербурге. Историческое здание в стиле классицизма, построенное в 1804-1807 годах по проекту Джакомо Кваренги для выучки и парадных выездов Лейб-гвардии Конного полка. Сегодня здесь размещается центральный выставочный зал «Манеж».'}
                img={one_3}
                img_1={one_3_2}
                name={'Конногвардейский манеж'}
                 end={<div style={{"fontWeight":"bold","fontSize":"120%"}}><p>Поскольку Диоскуры изображают обнаженных языческих богов — детей Зевса, после окончания строительства Исаакиевского собора по требованию православного духовенства их перенесли на задний фасад Конногвардейского манежа, и там они простояли вплоть до 1954 года.</p></div>}
              
                text={<div style={{"fontWeight":"bold","fontSize":"120%"}}><p align="center">Приготовьтесь, сейчас вы узнате 3 самых важных и интересных фактов об этом месте. И мы начинаем!:))</p><p>Казармы Лейб-гвардии Конного полка, особо приближенного к императорскому двору войскового соединения, изначально располагались на берегу канала, соединявшего Адмиралтейство с Новой Голландией, но позже засыпанного. Именно на месте бывшего канала и был построен манеж для зимней и летней выучки, а также парадных конных выездок этого полка. Он должен был стать украшением Исаакиевской площади, поэтому к строительству и оформлению манежа подошли со всей возможной серьезностью и монументальностью.<br/>Долгое время существовала легенда, что между Зимним дворцом и Конногвардейским манежем был прорыт высокий туннель, по которому можно было проехать верхом на лошади..</p> </div> }
                pollQuestions={"Как вы думаете кто такие диоскуры?"}
                answer_1={"Элемент украшения фасада здания"}
                answer_2={"Братья близнецы в древнегреческой мифологии, дети Зевса"}
                answer_3={"В древнегреческой мифологии злые фурии"}
                id_marker={"one_3"}
                but={<Button level={"commerce"} onClick={this.submit}>{this.state.nameButton}</Button>}
                position={{lat: 59.934304, lng: 30.303386}}
                 />



         /*     <Marker onClick={this.onMarkerClick}
              //  position={{ lat: this.state.lat, lng: this.state.lon }}
                id_marker={"I"}
                icon={{
                  url:MyIcon,
                  anchor: new this.props.google.maps.Point(32,32),
                  scaledSize: new this.props.google.maps.Size(32,51)
                     }} 
                name={'Это вы'} />
*/

        <InfoWindowEx
          marker={this.state.activeMarker}
          visible={this.state.showingInfoWindow}>
            <Div align="center">
            <img src={this.state.selectedPlace.img} style={{"width":"90%","height":"90%"}}/>
            <h1>{this.state.selectedPlace.name}</h1>
              <p>{this.state.selectedPlace.pretitle}</p>
              {this.props.errorGeo}
               {this.state.selectedPlace.but}
              </Div>
            
        </InfoWindowEx> 
      </Map>
    );
  }
}
 
export default GoogleApiWrapper({
  apiKey: ('AIzaSyCL0U2diIKJtBb5gmgyUsRvpSIIKIfKCv0')
})(MapContainer)